.request {
  &__container {
    padding-top: 5px;
    width: 322px;
    height: 340px;
    border: 1px solid #cd8e31;
    background-color: rgba(247, 236, 212, 0.836);
    margin: 0 auto;
    text-align: center;
    position: absolute;
    z-index: 10;
    font-family: Verdana, sans-serif;
    font-size: 14px;
    font-weight: 500;
    top: 31px;
    right: 21px;
    display: none;
  }
}

.resume {
  &__container {
    padding-top: 5px;
    width: 322px;
    height: 294px;
    border: 1px solid #cd8e31;
    background-color: rgba(247, 236, 212, 0.836);
    margin: 0 auto;
    text-align: center;
    position: absolute;
    z-index: 10;
    font-family: Verdana, sans-serif;
    font-size: 14px;
    font-weight: 500;
    top: 69px;
    right: 21px;
    display: none;
  }
}

.form {
  &__input {
    width: 306px;
    height: 32px;
    background-color: #fff;
    color: #55493f;
    border: 1px solid #cd8e31;
    margin: 7px;
    font-size: 14px;
    padding: 2px 10px;
    line-height: 28px;
  }

  &__select {
    width: 306px;
    height: 32px;
    margin: 0 auto;
    position: relative;
    background-color: #fff;
    border: 1px solid #cd8e31;
    margin: 7px;
  }

  &__header {
    display: flex;
    align-items: center;
    position: relative;
  }

  &__current {
    padding: 0 10px;
    font-family: Verdana;
    font-size: 14px;
    line-height: 1.2;
    color: #55493f;
  }

  &__select-body {
    background-color: #fcebc6;
    border: 1px solid #b1b1b1;
    overflow: hidden;
    position: absolute;
    z-index: 1;
    display: none;
    cursor: pointer;
    left: -1px;
    top: 31px;
    width: 306px;
    border: 1px solid #cd8e31;
    z-index: 12;
  }

  &__item {
    height: 30px;
    line-height: 28px;
    background-color: #fcebc6;
    padding: 2px 12px;
    font-size: 18px;
    color: #45250a;
    transition: all 0.3s;
    cursor: pointer;
    text-align: left;
  }
  &__item:hover {
    background-color: #d8af75;
    transition: all 0.3s;
  }

  &__text {
    width: 306px;
    height: 74px;
    background-color: #fff;
    color: #55493f;
    border: 1px solid #cd8e31;
    font-size: 14px;
    padding: 2px 10px;
    line-height: 28px;
    resize: none;
    margin-bottom: 31px;
  }

  &__button {
    text-transform: uppercase;
    display: inline-block;
    height: 24px;

    background: linear-gradient(
      to top,
      rgb(164, 103, 31) 0%,
      rgb(131, 76, 11) 14%,
      rgb(119, 71, 11) 25%,
      rgb(176, 120, 43) 55%,
      rgb(233, 192, 135) 100%
    );
    box-shadow: 0px 2px 0px 0px rgba(126, 79, 19, 0.004);
    color: #fff;
    border: 1px solid #ab6e17;
    width: 141px;
    height: 24px;
    text-transform: uppercase;
    font-size: 14px;
    border-radius: 4px;
    margin: 0 45px 0 49px;
    transition: all 0.3s;
    cursor: pointer;
  }
}

.form__select.is-active .form__select-body {
  display: block;
}

.request__container.is-active,
.resume__container.is-active {
  display: block;
}

.form__input[type="number"]::-webkit-inner-spin-button,
.form__input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.form__input[type="number"] {
  -moz-appearance: textfield;
}

.form {
  &__file {
  }
}
.file {
  &__lable {
    width: 306px;
    height: 32px;
    opacity: 1;
    background-color: #fff;
    color: #55493f;
    border: 1px solid #cd8e31;
    margin: 7px;
    font-size: 14px;
    padding: 2px 10px;
    line-height: 28px;
    display: block;
    cursor: pointer;
    text-align: left;
    margin-bottom: 25px;
  }

  &__input {
    display: none;
  }

  &__text {
    font-family: Verdana;
    line-height: 1.2;
    color: #55493f;
  }
}
