.search {
  &_btn {
    background: linear-gradient(
      to top,
      rgb(164, 103, 31) 0%,
      rgb(131, 76, 11) 14%,
      rgb(119, 71, 11) 25%,
      rgb(176, 120, 43) 55%,
      rgb(233, 192, 135) 100%
    );
    box-shadow: 0px 2px 0px 0px rgba(126, 79, 19, 0.004);
    color: #fff;
    border: 1px solid #ab6e17;
    width: 141px;
    height: 24px;
    text-transform: uppercase;
    font-size: 14px;
    border-radius: 4px;
    margin: 0 45px 0 49px;
    transition: all 0.3s;
    cursor: pointer;
  }
  &_btn:hover {
    background: linear-gradient(
      to top,
      rgb(250, 237, 199) 0%,
      rgb(234, 207, 143) 13%,
      rgb(232, 196, 120) 58%,
      rgb(255, 249, 224) 96%,
      rgb(255, 249, 224) 100%
    );
    color: #45250a;
  }

  &__container {
    background-color: #fff7e1;
    padding: 30px 0;
    text-align: center;
  }

  &__title {
    font-family: Book Antiqua, sans-serif;
    font-size: 25px;
    padding-bottom: 39px;
  }

  &__filter {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #f6e2d1;
    border: 1px solid #c4c0a5;
    border-radius: 2px;
    max-width: 864px;
    margin: 0 auto;
    position: relative;
    font-family: Verdana, sans-serif;
    font-weight: 500;
    padding: 0 20px;
  }
  &__filter::before {
    content: "";
    background-image: url('../img/staff/filter-befor.png');
    top: -11px;
    left: -11px;
    width: 66px;
    height: 67px;
    position: absolute;
  }
  &__filter::after {
    content: "";
    background-image: url('../img/staff/filter-after.png');
    top: -11px;
    right: -11px;
    width: 66px;
    height: 67px;
    position: absolute;
  }
}

.left__text {
  font-size: 18px;
  padding: 26px 0px 18px;
}
.filter {
  &__left {
    margin-bottom: 18px;
  }

  &__text {
    font-size: 18px;
    padding-bottom: 9px;
  }

  &__right {
    font-size: 18px;
    max-width: 325px;
    text-align: right;
    align-items: center;
  }

  &__input {
    padding: 9px 9px 9x 7px;
    width: 39px;
    height: 31px;
    border: 1px solid #b1b1b1;
    text-align: center;
    color: #45250a;
    font-size: 18px;
    line-height: 18px;
  }
}

.select {
  margin: 0 auto;
  position: relative;
  width: 200px;
  height: 32px;

  &__header {
    display: flex;
    align-items: center;
    position: relative;
    border: 1px solid #ab6e17;
    background-color: #fff;
    text-align: left;
    font-size: 18px;
    line-height: 20px;
    padding-left: 6px;
    z-index: 2;
    cursor: pointer;
  }

  &__header:focus {
    outline: auto;
  }

  &__current {
    font-size: 18px;
    line-height: 24px;
  }

  &__icon {
    background-image: url('../img/staff/select.png');
    background-repeat: no-repeat;
    background-position: right;
    flex-shrink: 0;
    margin-left: auto;
    width: 27px;
    height: 30px;
  }

  &__body {
    background-color: #fcebc6;
    border: 1px solid #b1b1b1;
    overflow: hidden;
    width: 230px;

    border-top: 0;
    display: none;
    left: 0;
    position: absolute;
    top: 0;
    z-index: 1;
    cursor: pointer;
  }

  &__item {
    height: 30px;
    line-height: 28px;
    background-color: #fcebc6;
    padding: 2px 12px;
    font-size: 18px;
    color: #45250a;
    transition: all 0.3s;
    width: 230px;
    cursor: pointer;
    text-align: left;
  }
  &__item:hover {
    background-color: #d8af75;
    transition: all 0.3s;
  }
}
.select.is-active .select__body {
  display: block;
}
