@import "slider.scss";
@import "nav.scss";
@import "top-header.scss";
@import 'form.scss';


body {
  background-image: url(@img/index/bg-img.png);
  background-size: cover;
  background-position: center;
  background-repeat: repeat-y;
}


.form__resume_container {
  padding-top: 5px;
  width: 322px;
  height: 295px;
  border: 1px solid #cd8e31;
  background-color: rgba(247, 236, 212, 0.836);
  margin: 0 auto;
  text-align: center;
  position: absolute;
  z-index: 10;
  font-family: Verdana, sans-serif;
  font-size: 14px;
  font-weight: 500;
  top: 69px;
  right: 21px;
  display: none;
}

input.form__input {
  width: 306px;
  height: 32px;
  background-color: #fff;
  color: #55493f;
  border: 1px solid #cd8e31;
  margin: 7px;
  font-size: 14px;
  padding: 2px 10px;
  line-height: 28px;
}

.resume__form-btn {
  width: 306px;
  height: 32px;
  opacity: 1;
  background-color: #fff;
  color: #55493f;
  border: 1px solid #cd8e31;
  margin: 6px;
  font-size: 14px;
}

.resume__form-list {
  left: 7px;
  top: 32px;
  width: 306px;
  border: 1px solid #cd8e31;
  text-transform: initial;
}

.resume__form-item {
  width: 306px;
  height: 32px;
  font-size: 14px;
}

button.form-btn {
  text-transform: uppercase;
  display: inline-block;
  height: 24px;
}

.lable__btn {
  width: 306px;
  height: 32px;
  opacity: 1;
  background-color: #fff;
  color: #55493f;
  border: 1px solid #cd8e31;
  margin: 7px;
  font-size: 14px;
  padding: 2px 10px;
  line-height: 28px;
  display: block;
  cursor: pointer;
  text-align: left;
  margin-bottom: 25px;
}

.example-2 .input-file {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.form__client_container {
  padding-top: 5px;
  width: 320px;
  height: 339px;
  border: 1px solid #cd8e31;
  background-color: rgba(247, 236, 212, 0.836);
  margin: 0 auto;
  text-align: center;
  border: 1px solid #cd8e31;
  position: absolute;
  top: 34px;
  right: 21px;
  z-index: 10;
  font-family: Verdana, sans-serif;
  font-size: 14px;
  font-weight: 500;
  display: none;
}

.client__form-btn {
  width: 306px;
  height: 32px;
  opacity: 1;
  background-color: #fff;
  color: #55493f;
  border: 1px solid #cd8e31;
  margin: 7px 7px 12px;
  font-size: 14px;
  padding: 2px 10px;
  line-height: 28px;
  z-index: 11;
}

.client__form-list {
  left: 7px;
  top: 32px;
  width: 306px;
  border: 1px solid #cd8e31;
  text-transform: initial;
  z-index: 12;
}

.client__form-item {
  width: 306px;
  height: 32px;
  font-size: 14px;
}

button.client-btn {
  text-transform: uppercase;
  display: inline-block;
  height: 24px;
  line-height: 24px;
}

.lable__btn {
  width: 306px;
  height: 32px;
  opacity: 1;
  background-color: #fff;
  color: #55493f;
  border: 1px solid #cd8e31;
  margin: 7px;
  font-size: 14px;
  padding: 2px 10px;
  line-height: 28px;
  display: block;
  cursor: pointer;
  text-align: left;
  margin-bottom: 25px;
}

textarea::-webkit-input-placeholder {
  color: #55493f;
  font-size: 14px;
}

textarea::-moz-placeholder {
  color: #55493f;
  font-size: 14px;
}

textarea:-ms-input-placeholder {
  color: #55493f;
  font-size: 14px;
}

textarea::-ms-input-placeholder {
  color: #55493f;
  font-size: 14px;
}

textarea::placeholder {
  color: #55493f;
  font-size: 14px;
}

.client__form-text {
  width: 306px;
  height: 74px;
  background-color: #fff;
  color: #55493f;
  border: 1px solid #cd8e31;
  font-size: 14px;
  padding: 2px 10px;
  line-height: 28px;
  resize: none;
  margin-bottom: 31px;
}

.form__resume_container.is-active {
  display: block;
}

.form__client_container.is-active {
  display: block;
}
