.footer__top {
  background-color: #4e2c1e;
  background-image: linear-gradient(0deg, #4e2c1e 0%, #4e2c1e 100%);
  color: #fff;
  font-size: 11px;
  text-transform: uppercase;
  text-shadow: 0px 1px 0px rgba(17, 17, 26, 0.46);
  box-shadow: 0px 1px 1px 0px rgba(17, 17, 26, 0.86);
  border-top: 1px solid #62483a;
  padding: 19px 0px;
}

.footer__list {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.footer__bottom {
  display: flex;
  background: linear-gradient(
    to bottom,
    rgb(127, 93, 66) 0%,
    rgb(166, 132, 94) 100%
  );
  color: #ffffff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__bottom a.footer__tel {
  color: #ffffff;
  padding-left: 40px;
  font-size: 19px;
  font-family: GaramondC, sans-serif;
}

.footer__logo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px 34px;
}

.footer__text {
  font-size: 11px;
}

a.footer__link {
  position: relative;
  font-size: 11px;
  color: #ffffff;
  text-transform: uppercase;
  text-shadow: 0px 1px 0px rgba(17, 17, 26, 0.46);
  line-height: 11px;
}

a.footer__link::after {
  content: "";
  position: absolute;
  top: 2px;
  right: -43px;
  width: 1px;
  height: 10px;
  background-color: #ffffff;
}
