.personal {
  &__bg {
    max-width: 923px;
    margin: 0 auto;
    background-color: #fffde8;
  }

  &__table {
    margin: 0 auto;
    border-collapse: collapse;
    font-family: Verdana, sans-serif;
    margin-bottom: 40px;
  }

  &__title {
  }

  &__content {
    font-size: 15px;
    line-height: 15px;
    height: 31px;
    text-align: center;
  }

  &__name {
    min-width: 346px;
    text-align: left;
    padding-left: 5px;
  }

  &__post {
    min-width: 228px;
  }

  &__age {
    min-width: 142px;
  }

  &__experience {
    min-width: 160px;
    text-align: right;
    padding-right: 55px;
  }

  &__text {
    font-size: 14px;
    color: #4e2c1e;
    text-align: justify;
    word-spacing: 11px;
    padding: 0 20px 20px 20px;
  }

  &__text:last-child {
    padding-bottom: 138px;
  }
}

.title {
  &__cell {
    text-transform: uppercase;
    font-size: 15px;
    color: #fff;
    background-color: #814b04;
    text-align: center;
    border: 1px solid #d3a86c;
    height: 29px;
    line-height: 15px;
  }
}

.personal__content:nth-child(2n) {
  background-color: #fff;
  border: 1px solid #d3a86c;
}

.personal__content:nth-child(2n + 1) {
  background-color: rgba(0, 0, 0, 0);
  color: #814b04;
}

.personal__content:nth-child(2n + 1) .personal__name,
.personal__content:nth-child(2n + 1) .personal__post,
.personal__content:nth-child(2n + 1) .personal__age {
  border-right: 1px solid #d3a86c;
}
