.slider {
  &__container {
    display: flex;
    align-items: center;
    position: relative;
    filter: drop-shadow(0px 3px 2px rgba(0, 0, 0, 0.3));

  }

  &__images {
    display: flex;
    transition: all 1s ease;

  }

  &__image {
    width: 925px;
    height: 324px;
    object-fit: cover;

  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 19px;
    height: 30px;
    background-color: #fff7e1;
    padding-top: 7px;
  }

  &__button {
    width: 13px;
    height: 13px;
    background-color: rgba(209, 115, 0, 0.5);
    box-shadow: inset 0px 2px 3px 0px rgba(51, 27, 2, 0.74);
    border-radius: 50%;
    border: none;
    cursor: pointer;
    transition: all 0.2s;
  }

  &__button-active {
    width: 16px;
    height: 16px;
    background-color: #7c5a4c;
    border: 1px solid #fff;
    border-radius: 50%;
    transition: all 0.2s;
  }
}

.arrow {
  height: 324px;
  cursor: pointer;
  opacity: 0;
  z-index: 3;
}
.left {
  position: absolute;
  top: 0px;
  left: 0px;
  transform: rotate(-180deg);
}

.right {
  position: absolute;
  top: 0px;
  right: 0px;
}

.frame {
  width: 100%;
  height: 324px;
  align-items: center;
  overflow: hidden;
}
