.header {
  &__top {
    background-color: #4e2c1e;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__tell {
    font-family: Book Antiqua, sans-serif;
    font-size: 26px;
    line-height: 1.2;
    padding: 24px 99px 21px 21px;
    color: #ffffff;
  }

  &__buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: Book Antiqua, sans-serif;
    font-size: 16px;
    font-weight: 600;
    position: relative;
  }

  &__button {
    width: 173px;
    height: 24px;
    background-color: #d7c074;
    background-image: linear-gradient(
      0deg,
      #faedc7 0%,
      #eacf8f 13%,
      #e8c478 63%,
      #fff9e0 100%
    );
    color: #472800;
    border: 1px solid #d8c992;
    border-radius: 4px;
    margin: 3px 22px;
    transition: all 0.7s;
    cursor: pointer;
  }

  &__button:hover {
    background-image: linear-gradient(
      0deg,
      #a4671f 0%,
      #834c0b 10%,
      #77470b 30%,
      #b0782b 61%,
      #e9c087 100%
    );
    box-shadow: 0px 1px 0px 0px #7e4f13;
    color: #ffffff;
  }
}
