.article {
  padding: 0px 24px;
  color: #4e2c1e;

  &__title {
    font-family: Arial, sans-serif;
    font-size: 24px;
    font-weight: 500;
    padding-bottom: 20px;
  }

  &__text {
    word-spacing: 11px;
    text-align: justify;
    font-size: 14px;
    line-height: 1.2;
    padding-bottom: 20px;
  }

  &__text:last-child{
    padding-bottom: 110px;
  }

}
