.gallery {
  &__container {
    display: flex;
    padding-bottom: 37px;
    justify-content: center;
    align-items: center;
  }

  &__item {
    width: 180px;
    height: 188px;
    border: 1px solid #a08357;
    background-color: #ffffff;
    text-align: center;
    overflow: hidden;
    margin: 0px 12px;
  }

  &__img {
    display: inline-block;
  }
}
