.header {
  &__nav {
    width: 925px;
    height: 39px;
    background-color: #8b5814;
    background-image: linear-gradient(
      0deg,
      #a4671f 0%,
      #834c0b 10%,
      #77470b 30%,
      #b0782b 61%,
      #e9c087 100%
    );
    box-shadow: 0px 1px 0px 0px #7e4f13;
    text-transform: uppercase;
    color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }
}

.nav {
  &_logo-img {
    position: absolute;
    bottom: -95%;
    right: 36%;
    z-index: 10;
    width: 284px;
  }

  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__links {
    background-image: linear-gradient(
      0deg,
      #a4671f 0%,
      #834c0b 10%,
      #77470b 30%,
      #b0782b 61%,
      #e9c087 100%
    );
    height: 40px;
    border: 1px solid #ccc;
    border-right: 1px solid #ccaf86;
    padding: 4px 27px;
    transition: all 0.7s;
  }

  &__links:first-child {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
  }
  &__links:last-child {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
  }

  &__links:hover {
    background-color: #d7c074;
    background-image: linear-gradient(
      0deg,
      #faedc7 0%,
      #eacf8f 13%,
      #e8c478 63%,
      #fff9e0 100%
    );
  }

  &__link {
    color: #ffffff;
    font-size: 11px;
    font-family: Verdana, sans-serif;
    line-height: 15px;
    text-align: center;
    display: inline-block;
    transition: all 0.7s;
  }

  &__span {
    line-height: 30px;
  }

  &__links-list {
    display: none;
    border: 1px solid #bfa485;
    z-index: 5;
    position: absolute;
    top: 40px;
    left: 109px;
  }

  &__links-item {
    width: 190px;
    color: #45250a;
    font-size: 11px;
    font-family: Verdana, sans-serif;
    line-height: 15px;
    text-align: left;
    text-transform: capitalize;
    background-color: #fcebc6;
    padding: 5px 11px;
    transition: all 0.3s;
  }

  &__links-item:hover {
    background-color: #d8af75;
    transition: all 0.5s;
  }

  &__list-link {
    color: #45250a;
    font-size: 14px;
  }

  &__links-logo {
    min-width: 215px;
  }
}

.nav__links:hover a.nav__link {
  color: #4e2c1e;
}

.nav__links:hover .nav__links-list,
.nav__links-list:hover {
  display: block;
}
