.personal {
  &__info {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-image: url('../../img/index/bg-two.png');
    background-repeat: no-repeat;
    background-position: top;
    padding: 0px 22px 47px 22px;
    max-width: 891px;
    margin: 0 auto;
  }
}
.left-info,
.right-info {
  max-width: 374px;
}

.left-info .info__img {
  float: right;
  padding: 26px 0px 12px 18px;
}

.left-info .info__btn {
  margin: 0 0 15px 248px;
}

.right-info .info__title {
  margin-left: 50px;
}

.right-info .info__img {
  float: left;
  padding: 15px 24px 8px 10px;
}

.right-info .info__btn {
  margin: 0 250px 15px 0;
}

.info {
  &__title {
    font-family: GaramondC, sans-serif;
    font-size: 23px;
    color: #5e3625;
    max-width: 145px;
    line-height: 24px;
    padding: 35px 0px 20px;
    display: inline-block;
  }

  &__img {
  }
  &__text {
    margin-bottom: 7px;
  }

  &__btn {
    min-width: 126px;
    min-height: 25px;
    background: linear-gradient(
      to top,
      rgb(250, 237, 199) 0%,
      rgb(234, 207, 143) 13%,
      rgb(232, 196, 120) 58%,
      rgb(255, 249, 224) 96%,
      rgb(255, 249, 224) 100%
    );
    color: #45250a;
    border: 1px solid #c7a865;
    border-radius: 4px;
    margin: 3px 22px;
    display: inline-block;
    transition: all 0.7s;
    font-weight: 700;
    cursor: pointer;
  }

  &__btn:hover {
    background: linear-gradient(
      to top,
      rgb(164, 103, 31) 0%,
      rgb(131, 76, 11) 14%,
      rgb(119, 71, 11) 25%,
      rgb(176, 120, 43) 55%,
      rgb(233, 192, 135) 100%
    );
    color: #fff;
    border: 1px solid #ccc;
    transition: all 0.7s;
  }
}
