@import "nullstyle";
@import "fonts";

@import "./header/header.scss";

@import "./mainIndex/mainIndex.scss";
@import "./mainStaff/mainStaff.scss";

@import "./mainStaff/mainStaff.scss";
@import "./footer/footer.scss";

body {
  color: #4e2c1e;
  font-size: 13px;
  line-height: 16px;
  font-family: Arial, sans-serif;
  background-image: url("../img/bg-img.png");
}

.wrapper {
  min-width: 100vw;
  margin: 0 auto;
}

.container-shadow {
  box-shadow: -12px 0px 10px -8px rgba(34, 60, 80, 0.2),
    12px 0px 10px -8px rgba(34, 60, 80, 0.2);
  width: 925px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.main {
  flex-grow: 1;
}

.main-bg {
  background-color: #fff7e1;
}

a {
  color: #8d8d8d;
  line-height: 26px;
}
